import VueRouter from 'vue-router'
import Login from '../Login/MainLogin.vue'
import MainPage from '../View/MainPage.vue'
import BomQuery from '@/View/BomQuery.vue'
import BomManage from '@/View/BomManage.vue'
import Whereuse from '@/View/WhereUse.vue'
import ProductManage from '@/View/ProductManage.vue'
import ProducePlan from '@/View/ProducePlan.vue'
import ProductOrder from '@/View/ProductOrder.vue'
import Vue from 'vue'
import MaterialMan from '@/View/MaterialMan.vue'
import MaterialOrder from '@/View/MaterialOrder.vue'
import NeedPlan from '@/View/NeedPlan.vue'
import InformationSearch from '@/View/InformationSearch.vue'
import { getInfo } from '@/utils/storage'
Vue.use(VueRouter)


const router = new VueRouter({
  routes: [
    {
      path: '/login', component: Login,
    },
    {
      path: '/', component: MainPage,
      children: [
        {
          path: '/mainpage/bommanage', component: BomManage
        },
        {
          path: '/mainpage/bomquery', component: BomQuery
        },
        {
          path: '/mainpage/whereuse', component: Whereuse
        },
        {
          path: '/mainpage/producemanage', component: ProductManage
        },
        {
          path: '/mainpage/produceplan', component: ProducePlan
        },
        {
          path: '/mainpage/productorder', component: ProductOrder
        },
        {
          path: '/mainpage/materialman', component: MaterialMan
        },
        {
          path: '/mainpage/materialorder', component: MaterialOrder
        },
        {
          path: '/mainpage/materialman', component: MaterialMan
        },
        {
          path: '/mainpage/needplan', component: NeedPlan
        },
        {
          path: '/mainpage/informationsearch', component: InformationSearch
        },
      ]
    },

  ]
})

// router.beforeEach((to) => {
//   const useStore = getInfo()
//   if (!useStore.token && to.path !== '/login')
//     return '/login'
// })
//正确例子
router.beforeEach((to, from, next) => {
  const useStore = getInfo()
  if (to.path !== '/login' && !useStore.token) {
    next({ path: '/login' })
  }
  else {
    next()
  }
})

export default router