<template>
  <div class="Main">
<!-- 左边标题部分 -->
<div   class="main-login-left" >
  <p class="big">智慧生产系统</p>
  <p class="small">天津大学仁爱学院 </p>
</div>
<!-- 右边登录注册部分 -->
<div class="main-login-right">
  <!-- 登入部分 -->
  <div class="login"  ref="form" v-if="isShowedit">
    <h1>登录</h1>
    <div class="login-input">
      <!-- 输入部分 -->
      <p>账户</p>
      <input 
      type="text" 
      placeholder="请输入用户名" 
      id="count"
      v-model="username"
      >
      <label for="" v-show="this.username==''">请输入账号</label>
      <p>密码</p>
      <input
       type="password"
       placeholder="请输入密码"
       id="password"
       v-model="password"
       >
      <label for="" v-if="this.password==''">请输入密码</label>
      <!-- 忘记密码 & 记住密码 -->
      <div class="login-forgetpassword">
        <span>记住我 </span>
        <input type="checkbox" id="rembember">
        <a class="forget" href="#">忘记密码?</a>
      </div>
    </div>
    <button type="button" class="button-login"
    @click="login">登入</button>
    <!-- 注册 -->
    <div class="login-zuce" >
      <a href="#" class="a-zuce" @click="Change">注册 -></a>
    </div>
  </div>

  <div class="register" ref="form" v-else>

<h1>注册</h1>
<div class="login-input">
  <!-- 输入部分 -->
  <p>管理用户名</p><input type="text" placeholder="请输入用户名" id="count" form="">
  <p>密码</p><input type="password" placeholder="请输入密码" id="password" form="">
  <p>再次输入密码</p><input type="password" placeholder="请再次输入密码" id="passwordagain">
</div>

<button type="button" class="button-login" >注册</button>

<!-- 注册 -->
<div class="login-zuce">
  <a href="#" class="a-zuce" @click="Change" >返回</a>
</div>
</div>
</div>
</div>
</template>

<script>
import { codeLogin } from "@/api/login";
// import  request from "@/utils/request";
import { Message } from 'element-ui'
export default {

  
  props:['isshow'],


//  async created(){
//   const res= await request.get('/my/menus')
//   console.log(res);
//   },

    data(){
    return{

      username:'',
      password:'',
      isShowedit: true
    }
  },
  methods:{
    Change(){
      this.isShowedit = !this.isShowedit
    },
    async login(){
      if (! /^.{3,9}$/.test(this.username)) {
        Message.error('账号必须是3-9位的正确账号')
        return
      }
      if (! /^.{6,15}$/.test(this.password)) {
        Message.error('密码必须是6-15位非空字符')
        return
      }
      const res = await codeLogin(this.username, this.password)
      this.$store.commit('user/setUserInfo',res.data)
      console.log(res)
      Message.success('登入成功')
      this.$router.push('/')
      }

    
    }
  //   comebacklogin(){
  // console.log(this.isshownow);
  //   }
  }
  

  


</script>

<style>
 * {
    margin: 0;
    padding: 0;
  }

  .Main {
    display: flex;
    flex-direction: row;
    height: 100vh;
  }

  .main-login-left {
    flex: 1;
    background: rgb(5, 223, 243, 0.3);
    border-radius: 20px;
    overflow: hidden;
    background-image:url('../assets/backgroundimg.png');
    opacity: 0.7;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .main-login-left img {
    opacity: 0.3;
  }

  .main-login-left .big {
    text-align: center;
    font-size: 200px;
    color: white;
    font-family: '楷体';
  }

  .main-login-left .small {
    text-align: center;
    font-size: 50px;
    color: white;
    font-family: '楷体';
  }

  .main-login-right {
    flex: 1;
    display: flex;
    background-color: rgb(255, 255, 255);
  }

  .main-login-right .login {
    position: relative;
    width: 60%;
    height: 75%;
    /* background-color: aqua; */
    margin: auto;
    margin-bottom: 10px;
  }

  .main-login-right .register {
    position: relative;
    width: 60%;
    height: 75%;
    /* background-color: aqua; */
    margin: auto;
    margin-bottom: 10px;
  }

  .login h1:after,
  .login h1:before {
    content: "";
    display: block;
    clear: both;
  }

  .main-login-right h1 {
    letter-spacing: 2px;
    font-size: 40px;
    margin-left: 0px;

    font-family: '黑体';
  }

  .main-login-right .login-input {
    position: relative;
    margin-top: 20px;
    height: 350px;
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    align-items: center;

  }

  #count {
    height: 40px;
    width: 100%;
    border-radius: 5px;
    border: rgba(203, 200, 200, 0.934) solid 2px;
    font-size: 15px;
  }

  .main-login-right label{
  width: 100%;
  height: 20px;
  color: red;
 }

  #password {
    height: 40px;
    width: 100%;
    border-radius: 5px;
    border: rgba(203, 200, 200, 0.934) solid 2px;
    font-size: 15px;
  }

  #passwordagain {
    height: 40px;
    width: 100%;
    margin: 10px 10px 30px;
    border-radius: 5px;
    border: rgba(203, 200, 200, 0.934) solid 2px;
    font-size: 15px;
  }

  /* 输入框placehold字间距 */
  .login-input input::-webkit-input-placeholder {
    left: 20px;
    letter-spacing: 2px;
    padding-left: 20px;

  }



  .login-input p {
    margin-left: 5px;
    font-size: 20px;
    color: rgba(156, 151, 151, 0.934)
  }

  /* 记住我 找回密码 */
  .login-forgetpassword:after,
  .login-forgetpassword:before {
    content: "";
    display: block;
    clear: both;
    
  }

  .login-forgetpassword {
    margin-top: 70px;
    margin-bottom: 50px;
    display: flex;
    height: 30px;
    width: 100%;
    align-items: center;
  }

  .login-forgetpassword span {
    font-size: 15px;
  }

  .login-forgetpassword .forget {
    position: relative;
    margin-left: 230px;
    color: rgb(67, 101, 110, )
  }

  /* 记住密码 勾选框 */
  input[type='checkbox'] {
    /* padding-left: 100px; */
    margin-left: 5px;
    width: 17px;
    height: 17px;
    border: black 3px solid;
  }






  /* 登入按钮 */
  .button-login {
    width: 100%;
    height: 50px;
    background-color: rgb(121, 180, 197);
    color: white;
    font-size: 25px;
    font-family: '黑体';
    letter-spacing: 5px;
    border-radius: 5px;
    border: 1px rgb(67, 101, 110);
    margin-bottom: 30px;
  }

  .button-login:hover {
    background-color: rgb(121, 180, 197, 0.5);

  }

  .a-zuce {
    width: 30px;
    height: 10px;
  }

  .a-zuce:hover {
    color: rgb(67, 101, 110, 0.3);

  }
</style>